export default /* glsl */`
#ifdef USE_SHADOWMAP

	float nDotL;
	vec4 temp;

	#if NUM_DIR_LIGHTS > 0

	#pragma unroll_loop
	for ( int i = 0; i < NUM_DIR_LIGHTS; i ++ ) {

		vDirectionalShadowCoord[ i ] = directionalShadowMatrix[ i ] * worldPosition;
		temp = worldPosition;
	  nDotL = clamp( dot(lightDirection[ i ], vNormal), 0.0, 1.0);
    temp.xyz += normalize((modelMatrix * vec4( normal, 0.0 )).xyz) * 0.02 * directionalNormalOffset[ i ] * pow( 1.0 -  nDotL * nDotL, 4.);
    vDirectionalShadowCoord[ i ].xy = (directionalShadowMatrix[ i ] * temp).xy;
	}

	#endif

	#if NUM_SPOT_LIGHTS > 0


	#pragma unroll_loop
	for ( int i = 0; i < NUM_SPOT_LIGHTS; i ++ ) {

		vSpotShadowCoord[ i ] = spotShadowMatrix[ i ] * worldPosition;
		temp = worldPosition;
	  nDotL = clamp( dot(normalize(spotPosition[ i ] - mvPosition.xyz), vNormal), 0.0, 1.0);
    temp.xyz += normalize((modelMatrix * vec4( normal, 0.0 )).xyz) * 0.02 * spotNormalOffset[ i ] * pow( 1.0 -  nDotL * nDotL, 4.);
    vSpotShadowCoord[ i ].xy = (spotShadowMatrix[ i ] * temp).xy;
	}

	#endif

	#if NUM_POINT_LIGHTS > 0

	#pragma unroll_loop
	for ( int i = 0; i < NUM_POINT_LIGHTS; i ++ ) {

		vPointShadowCoord[ i ] = pointShadowMatrix[ i ] * worldPosition;

	}

	#endif

	/*
	#if NUM_RECT_AREA_LIGHTS > 0

		// TODO (abelnation): update vAreaShadowCoord with area light info

	#endif
	*/

#endif
`;
